import { iconFiles } from '../files';

const ICON_WRAPPER_CLASS = 'logpresso-icon-item';
const ICON_CLASS = 'lp-icon';
const SEARCH_ELEMENT = '.search-bar-input';
const COPY_TARGET = 'copy-target';

export class IconManager {
	iconElements: HTMLElement[] = [];
	private searchBar: HTMLInputElement = null;
	private renderTarget: Element;
	constructor(targetElement: Element) {
		this.init(targetElement);
		this.initEvent();
	}

	private initEvent(): void {
		this.searchBar.addEventListener('keyup', (e: KeyboardEvent) => {
			if (this.searchBar.value !== '') {
				this.iconElements.forEach((icon) => {
					const idxOf = icon.innerText.indexOf(this.searchBar.value);
					if( idxOf === -1) {
						icon.style.display = 'none'
					} else {
						icon.style.display = 'flex'
					}
				})
			} else {
				this.iconElements.forEach((icon) => icon.style.display = 'flex');
			}
		})

		this.renderTarget.addEventListener('click', (e) => {
			const target = (e.target as HTMLElement);
			if (target.classList.contains(COPY_TARGET)) {
				var tempElem = document.createElement('textarea');
				tempElem.value = `<i class="lp-icon">${target.innerText}</i>`;  
				document.body.appendChild(tempElem);
				tempElem.select();
				document.execCommand("copy");
				document.body.removeChild(tempElem);
			}
		})

	}

	private init(target: Element): void {
		this.searchBar = document.querySelector(SEARCH_ELEMENT);
		this.renderTarget = target;
		this.iconElements = iconFiles.map<HTMLElement>((icon) => {
			const div = document.createElement('div');
			const i = document.createElement('i');
			const span = document.createElement('span');
			div.className = ICON_WRAPPER_CLASS;
			i.className = ICON_CLASS;
			i.classList.add(COPY_TARGET);
			const iconName = icon.replace('.svg', '');
			i.innerHTML = iconName;
			span.innerText = iconName;
			span.className = COPY_TARGET;

			div.appendChild(i);
			div.appendChild(span);
			target.appendChild(div);

			return div;
		})
	}
}