
const SIZE_RANGE_ID = '#icon-size';
const SIZE_SHOW_CLASS = '.icon-size-text';
const COLOR_ID = '#icon-color';
const COLOR_SHOW_CLASS = '.icon-color-preview';

export class Custom {
	private sizeRange: HTMLInputElement = null;
	private sizeShow: HTMLSpanElement = null;
	private color: HTMLInputElement = null;
	private colorShow: HTMLDivElement = null;
	private sheet: CSSStyleSheet = null;

	constructor() {
		this.init();
		this.initEvent();
	}

	private initEvent(): void {
		this.sizeRange.addEventListener('input', (e) => {
			const value = this.sizeRange.value;
			this.sizeShow.innerText = value + 'px';

			this.sheet.removeRule(0);
			this.sheet.insertRule(`.logpresso-icon-item i {font-size: ${value}px; }`, 0);
		})

		this.color.addEventListener('keyup', (e) => {
			const color = this.color.value;
			const size = this.sizeRange.value;
			this.colorShow.style.background = color;

			this.sheet.removeRule(0);
			this.sheet.insertRule(
				`.logpresso-icon-item i {
					font-size: ${size}px;
					color: ${color};
				}`, 0
			);
		})
	}

	private init(): void {
		this.sizeRange = document.querySelector(SIZE_RANGE_ID);
		this.sizeShow = document.querySelector(SIZE_SHOW_CLASS);
		this.color = document.querySelector(COLOR_ID);
		this.colorShow = document.querySelector(COLOR_SHOW_CLASS);
		for (let sheet of document.styleSheets) {
			if (sheet.title === 'style') this.sheet = sheet;
		}
	}
}
